html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #84d9bc;
}

.bg-blanco {
  background-color: #FFFFFF;
}

.margen-menos-10 {
  margin-top: -10rem;
}

.pad-bot-0 {
  padding-bottom: 0 !important;
}

.seccion {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .seccion {
    padding: 8rem 0;
  }
}

.h2-sombra1 h2 {
  font-size: 3.6rem;
  display: inline-block;
}
.h2-sombra1 h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: 105%;
  display: block;
  margin: -1.4rem 0 0 0;
}

.h2-sombra2 h2 {
  font-size: 3.6rem;
  display: inline-block;
}
.h2-sombra2 h2::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: 105%;
  display: block;
  margin: -1.4rem 0 0 0;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
  text-align: center;
  text-transform: uppercase;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 2;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  list-style: none;
}

/* Añade a este archivo los demas archivos de esta carpeta con @forward y recuerda añadadirlos al archivo principal (app.scss) */
.site-header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("/build/img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding: 5rem 0;
}
.site-header .logo {
  max-width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .site-header .logo {
    margin: 0;
  }
}
.site-header .header-contenido {
  padding-top: 10rem;
  padding-bottom: 10rem;
}
@media (min-width: 1024px) {
  .site-header .header-contenido {
    padding-top: 15rem;
    padding-bottom: 15rem;
  }
}
.site-header .header-contenido h1 {
  color: #FFFFFF;
  font-size: 4rem;
}
@media (min-width: 768px) {
  .site-header .header-contenido h1 {
    font-size: 6rem;
    max-width: 50%;
  }
}
@media (min-width: 1024px) {
  .site-header .header-contenido h1 {
    max-width: 40%;
  }
}
.site-header .btn {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #000;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border-radius: 10px;
}
.site-header .btn:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .site-header .btn {
    width: auto;
    display: inline-block;
  }
}

@media (min-width: 768px) {
  .crear .crear-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
  .crear .crear-grid .crear-imagen {
    order: -1;
  }
}
.crear .crear-contenido {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .crear .crear-contenido {
    margin-bottom: 0;
  }
}

.negocio {
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .negocio .negocio-grid {
    display: grid;
    grid-template-columns: 60% 40%;
    gap: 3rem;
    align-items: center;
  }
}
.negocio .negocio-imagen {
  transition: 0.3s ease-in-out;
}
.negocio .negocio-imagen:hover {
  transform: rotate(4deg) scale(1.1);
}

.planes {
  background-color: #313c4e;
}
.planes h2 {
  color: #FFFFFF;
}
.planes .descripcion {
  color: #FFFFFF;
}

.nuestros-planes {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .nuestros-planes {
    flex-direction: row;
    max-width: 80rem;
    margin: 10rem auto 0 auto;
  }
}
.nuestros-planes .plan {
  flex-grow: 1;
  background-color: #FFFFFF;
  padding: 16rem 4rem 4rem 4rem;
  border-bottom: 2px solid #e1e1e1;
}
@media (min-width: 768px) {
  .nuestros-planes .plan {
    border: none;
    transition: 0.3s ease-in-out;
  }
  .nuestros-planes .plan:hover {
    transform: scale(1.1);
  }
}
.nuestros-planes .plan.plan-inicio {
  background-image: url(/build/img/icono_inicio.svg);
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.nuestros-planes .plan.plan-negocio {
  background-image: url(/build/img/icono_negocio.svg);
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
  background-color: #84d9bc;
  order: -1;
}
@media (min-width: 768px) {
  .nuestros-planes .plan.plan-negocio {
    margin: -5rem 0;
    padding-top: 21rem;
    border-radius: 15px;
    background-position: 50% 7rem;
    z-index: 100;
  }
  .nuestros-planes .plan.plan-negocio.plan-negocio {
    order: 0;
  }
}
.nuestros-planes .plan.plan-pro {
  background-image: url(/build/img/icono_pro.svg);
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.nuestros-planes .plan .precio {
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 1.8rem;
}
.nuestros-planes .plan .listado li {
  text-align: center;
  margin-bottom: 2rem;
}
.nuestros-planes .plan .listado li:last-of-type {
  margin-bottom: 0;
}
.nuestros-planes .plan .plan-boton {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border: 2px solid #FFFFFF;
  margin-top: 2rem;
}
.nuestros-planes .plan .plan-boton:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .nuestros-planes .plan .plan-boton {
    width: auto;
    display: inline-block;
  }
}
@media (min-width: 768px) {
  .nuestros-planes .plan .plan-boton {
    display: block;
  }
}

@media (min-width: 768px) {
  .testimoniales {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 4rem;
    align-items: center;
  }
  .testimoniales .imagen {
    grid-column: 1/3;
  }
  .testimoniales .contenido {
    grid-column: 3/6;
  }
}

.contenido {
  margin-top: 5rem;
}
.contenido .testimonial {
  background-color: #84d9bc;
  border: 4px solid #000;
  border-radius: 10px;
  padding: 2rem 5rem;
  margin-bottom: 3rem;
  position: relative;
}
@media (min-width: 768px) {
  .contenido .testimonial {
    max-width: 70%;
  }
  .contenido .testimonial:nth-child(2) {
    margin-left: 10%;
  }
  .contenido .testimonial:nth-child(3) {
    margin-left: 20%;
  }
  .contenido .testimonial:nth-child(4) {
    margin-left: 30%;
  }
}
.contenido .testimonial:last-of-type {
  margin-bottom: 0;
}
.contenido .testimonial::before {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(/build/img/comilla.svg);
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
}
.contenido .testimonial p {
  margin: 0;
  padding-left: 6rem;
}

.footer {
  background-color: #313c4e;
  color: #FFFFFF;
}
.footer .footer-grid {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .footer .footer-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 4rem;
  }
}
.footer .logo {
  max-width: 20rem;
  margin: 0 auto 5rem auto;
}
@media (min-width: 768px) {
  .footer .logo {
    margin-bottom: 0;
  }
}
.footer .footer-section {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .footer .footer-section {
    margin-bottom: 0;
  }
}
.footer .footer-section h3 {
  color: #84d9bc;
  text-align: left;
}
@media (min-width: 768px) {
  .footer .footer-section h3 {
    margin-bottom: 3rem;
  }
}
.footer .footer-section a {
  display: block;
  color: #FFFFFF;
  margin-bottom: 2rem;
}
.footer .footer-section a:last-of-type {
  margin-bottom: 0;
}
.footer .copyright {
  border-top: 3px solid #84d9bc;
  max-width: 120rem;
  margin: 0 auto;
}
.footer .copyright p {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
  padding: 1rem;
  font-size: 1.2rem;
  text-transform: uppercase;
}

